@use "../../foundation/variable" as v;

.p-scroll-anim {
	opacity: 0;

	&::before {
		position: absolute;
		content: "";
		display: block;
		width: 100%;
		height: 100%;
		background: url("../img/common/rabbit.png");
		background-color: v.$color--moon-shadow-start;
		filter: blur(5px);
		z-index: 10;
	}

	&.show {
		opacity: 1;

		&::before {
			animation: down 1s both;//早いvr
			// animation: down 2s both; //遅いvr

			@for $i from 1 to 4 {
				&:nth-child(#{$i}) {
					animation-delay: .5s * $i ;//早いvr
					// animation-delay: 500s * $i; //遅いvr
				}
			}
		}
	}
}

@keyframes down {
	0% {
		opacity: 1;
		transform: translate(0, 0);
	}
	50% {
		opacity: 1;
		transform: translate(0, 0);
	}
	100% {
		opacity: 0;
		transform: translate(300px, 0);
	}
}
