@use "../../foundation/variable" as v;
@use "sass:math";

.c-rotate-text {
  perspective: 800px;

  span {
    display: inline-block;
    transform-origin: 50% 70%;
    transform: rotateX(0deg);

    $count: 9;
    @for $i from 1 through $count {
      $delay: $i * .2 +s;
      $time:  10s;

      .char:nth-child(#{$i}) {
        animation: StandUp $time cubic-bezier(0.68, -0.55, 0.26, 1.55) $delay infinite;
      }
    }
  }
}


@keyframes StandUp {
  0% {
    transform: rotateY(90deg);//90deg:後ろに倒れてる
  }
  50% {
    transform: rotateY(-360deg) rotateX(0);
  }
  65% {
    transform: rotateY(360deg) rotateX(0);
  }
  100% {
    transform: rotateY(-360deg) rotateX(-90deg);
  }
}