.c-light {
  position: absolute;
  opacity: 0.5;
  left: 0%;
  width: 100%;
  height: 100%;
  background: radial-gradient(at 50% 0, rgba(229, 255, 0, 0.8), transparent);
  z-index: 30;
  animation: over 15s ease forwards;
}

@keyframes over {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}
.c-linear-light {
  position: relative;
  width: 300px;
  height: 3px;
}
.c-linear-light::before, .c-linear-light::after {
  content: "";
  position: absolute;
}
.c-linear-light:before {
  display: block;
  width: 100%;
  height: 100%;
  bottom: -50px;
  transform-origin: 0 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.1) 10%, rgba(255, 255, 255, 0.4) 80%, white);
  border-radius: 50%;
  animation: line 7s infinite;
}

@keyframes line {
  0% {
    transform: scaleX(0);
    opacity: 0.5;
  }
  90% {
    transform: scaleX(1);
    opacity: 1;
  }
  100% {
    transform: scaleX(1);
    opacity: 0;
  }
}
@keyframes light {
  0% {
    transform: scale(0, 0) translateX(52px);
    opacity: 1;
  }
  100% {
    transform: scale(2, 2) translateX(52px);
    opacity: 0;
  }
}
.c-moon {
  position: absolute;
  width: 80vw;
  height: 80vw;
  top: 15vmin;
  border-radius: 50%;
  background: yellow;
  filter: blur(4px);
  transition: all 1s linear;
}
