  .c-moon {
    width: 35vmin;
    height: 35vmin;
    top: 15vmin;
  }
}
.c-moon__txt {
  position: absolute;
  width: auto;
  color: yellow;
  font-family: "Gluten", cursive;
  font-size: 2.5rem;
  line-height: 2;
  letter-spacing: 5px;
  top: 53%;
  right: 5%;
}
