  .c-moon__txt {
    font-size: min(3.5vmin, 2.5rem);
    bottom: 18vmin;
  }
}
.c-moon::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-color: transparent;
  border-radius: 50%;
  -webkit-filter: blur(100px);
  filter: blur(100px);
}
.c-moon__waning {
  width: 102%;
  height: 100%;
  margin: auto;
  border-radius: 50%;
  filter: blur(4px);
  overflow: hidden;
}
.c-moon__waning::before {
  content: "";
  display: block;
  width: 110%;
  height: 100%;
  border-radius: 50%;
